<template>
<main>
    <Header :header="content"/>
    <h5 style="text-align:center;color:green;"> برای مثال تاریخ 1401/01/01 وارد شود</h5>
    <div>
    <p>
        <input  v-model="date"   data-jdp    placeholder=" تاریخ مدنظر را انتخاب نمایید">
        <button @click="getShift" class="btn btn-primary d-block mx-auto">مشاهده</button>
    </p>
    </div>
    <h5 style="text-align:center;color:red;">{{error}}</h5>
    <div class="container list" >
        <table class="table table-hover container" id="table">
            <thead>
                <tr>
                <th scope="col">نام</th>
                <th scope="col">کد پرسنلی</th>
                <th scope="col">سمت</th>
                <th scope="col">بخش</th>
                <th scope="col">قسمت</th>
                <th scope="col">شیفت</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                <td>{{name}}</td>
                <td>{{code}}</td>
                <td>{{who}}</td>
                <td>{{section}}</td>
                <td>{{div}}</td>
                <td>{{shift}}</td>
                </tr>
            </tbody>
        </table>
    </div>

    <button  v-print="'table'"  class="btn btn-primary d-block  mx-auto  my-2">چاپ شیفت</button>
 
</main>
</template>

<script>
jalaliDatepicker.startWatch();
import axios from 'axios'
import Header from '../../components/Header.vue'
import print from 'vue3-print-nb'
export default {
    name:'ShiftView',
    components:{Header},
    directives: {print},
    data() {
        return {
            content:'اطلاعات شیفت کاری',
            date:'',
            name:'',
            code:'',
            who:'',
            section:'',
            div:'',
            shift:'',
            error:''
        }
    },
    methods: {
         getShift(){
           if(this.date==='1401/01/01'){
            axios.get('../shift.json')
            .then(({data})=>{
                
                this.name=data.name;
                this.code=data.code;
                this.who=data.who;
                this.section=data.section;
                this.div=data.div;
                this.shift=data.shift;
                this.date='';
                this.error=''
            })
            
           }else{
                this.error='تاریخ مدنظر اشتباه است';
                this.date='';
                this.name='';
                this.code='';
                this.who='';
                this.section='';
                this.div='';
                this.shift='';
           }
        },

        print(){
            printJS('table', 'html')
        }

    },
    
      
}
</script>

<style lang="scss" scoped>
main{

    div{
        text-align: center;
            p{
                position: relative;
                display: inline-block;
                input{
                    
                    position:relative;
                    width: 30vw;
                    
                }
                button{
                    position: absolute;
                    left: 0;
                    top: 0;
                    bottom: 0;
                }

            }      

    }
}



.list{
        height: 300px;
        overflow-y: scroll;
        table{
            
            direction: rtl;
            background-color: rgb(255, 255, 255);
            border-radius: 2px;
            text-align: center;
    
                thead{
                    background-color: #082C68;
                    color: rgb(255, 255, 255);
                    
                    
        
                }
            
                    
        }
    }

</style>